  padding: 0;
  margin: 0;
  box-sizing: border-box;

  font-family: "Kanit", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-display: optional
}

body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

a,
input,
button,
textarea {
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  padding: 8px;
  background-color: #fff;
  color: #313131;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}

.nav {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: #313131;
}

.nav li {
  display: inline-block;
  padding: 10px;
}

.nav li a {
  text-decoration: none;
  color: #fff;
}

.brand {
  margin-right: auto;
  color: #fff;
}

.brand h2 {
  font-weight: 600;
}

.toggle_nav {
  color: #fff;
  margin-top: 10px;
  background-color: transparent;
  border: none;
}

.hero {
  min-height: 400px;
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.hero-inner {
  margin: 0 auto;
}

.hero-text {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
}

.hero-text h1 {
  font-size: 50px;
  font-weight: 500;
}

.hero-text button {
  margin-top: 15px;
  padding: 10px 10px 0px 10px;
  border-radius: 25px;
  border: 0;
  cursor: pointer;
}

.hero-text button a {
  font-size: 15px;
  text-decoration: none;
  color: #313131;
}

.content {
  padding: 20px;
}

/*
 * headline
 */

.headline__figure {
  width: 100%;
}

.headline__figure picture img {
  width: 100%;
}

.headline__figure figcaption {
  text-align: center;
  color: #666666;
  font-size: 13px;
  font-weight: 400;
  margin-top: 8px;
}

.headline__content {
  width: 100%;
  padding: 16px 0;
}

.headline__title {
  font-size: 24px;
  font-weight: 500;
}

.headline__description {
  font-size: 12px;
  margin-top: 12px;
}

.headline__button {
  text-transform: uppercase;
  margin-top: 24px;
  cursor: pointer;
  padding: 8px 16px;
  color: white;
  border: none;
  border-radius: 5px;
  background-color: #313131;
  transition: opacity 0.3s;
}

.headline__button:hover {
  opacity: 0.8;
}

.latest h1 {
  text-align: center;
  padding: 10px;
}

.product-item {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 100%;
  border-radius: 25px;
  overflow: hidden;
}

.product-item-content {
  padding: 20px;
}

.product-button {
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  margin-top: 24px;
  cursor: pointer;
  padding: 8px 16px;
  border: none;
  border-radius: 5px;
  background-color: #313131;
  transition: opacity 0.3s;
}

.product-name {
  font-weight: 600;
  font-size: 30px;
}

.product-rating {
  font-weight: 600;
  font-size: 20px;
}

.product-section {
  position: relative;
}

.product-section h3.product-city {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  background-color: #313131;
  color: #fff;
  padding: 10px 20px 10px 10px;
  border-radius: 0px 0px 25px 0px;
}

.product-section img.product-image {
  width: 100%;
  display: block;
}

/*
 * headline
 */

.detail__figure {
  width: 100%;
}

.detail__figure img {
  width: 100%;
}

.detail__figure figcaption {
  text-align: center;
  color: #666666;
  font-size: 13px;
  font-weight: 400;
  margin-top: 8px;
}

.detail__content {
  width: 100%;
  padding: 16px 0;
}

.detail__title {
  font-size: 24px;
  font-weight: 500;
}

.detail__description {
  font-size: 12px;
  margin-top: 12px;
}

.detail__subtitle {
  font-weight: 500;
}

.review {
  border: 5px solid #313131;
  padding: 15px;
  margin: 10px;
}

.review h3 {
  margin-top: 10px;
  font-weight: 500;
}

/*
  Like
*/
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #313131;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: white;
  cursor: pointer;
}

footer {
  padding: 10px;
  background-color: #313131;
}

footer p {
  text-align: center;
  color: #fff;
}

.hidden {
  display: none;
}

.error-message {
  color: red;
  font-size: 16px;
}

.loading-indicator {
  z-index: 9999;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.8);
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* HTML: <div class="loader"></div> */
.loader {
  width: 40px;
  aspect-ratio: 1;
  --c: linear-gradient(#000 0 0);
  --r1: radial-gradient(farthest-side at bottom, #000 93%, #0000);
  --r2: radial-gradient(farthest-side at top, #000 93%, #0000);
  background:
    var(--c), var(--r1), var(--r2),
    var(--c), var(--r1), var(--r2),
    var(--c), var(--r1), var(--r2);
  background-repeat: no-repeat;
  animation: l2 1s infinite alternate;
}

@keyframes l2 {

  0%,
  25% {
    background-size: 8px 0, 8px 4px, 8px 4px, 8px 0, 8px 4px, 8px 4px, 8px 0, 8px 4px, 8px 4px;
    background-position: 0 50%, 0 calc(50% - 2px), 0 calc(50% + 2px), 50% 50%, 50% calc(50% - 2px), 50% calc(50% + 2px), 100% 50%, 100% calc(50% - 2px), 100% calc(50% + 2px);
  }

  50% {
    background-size: 8px 100%, 8px 4px, 8px 4px, 8px 0, 8px 4px, 8px 4px, 8px 0, 8px 4px, 8px 4px;
    background-position: 0 50%, 0 calc(0% - 2px), 0 calc(100% + 2px), 50% 50%, 50% calc(50% - 2px), 50% calc(50% + 2px), 100% 50%, 100% calc(50% - 2px), 100% calc(50% + 2px);
  }

  75% {
    background-size: 8px 100%, 8px 4px, 8px 4px, 8px 100%, 8px 4px, 8px 4px, 8px 0, 8px 4px, 8px 4px;
    background-position: 0 50%, 0 calc(0% - 2px), 0 calc(100% + 2px), 50% 50%, 50% calc(0% - 2px), 50% calc(100% + 2px), 100% 50%, 100% calc(50% - 2px), 100% calc(50% + 2px);
  }

  95%,
  100% {
    background-size: 8px 100%, 8px 4px, 8px 4px, 8px 100%, 8px 4px, 8px 4px, 8px 100%, 8px 4px, 8px 4px;
    background-position: 0 50%, 0 calc(0% - 2px), 0 calc(100% + 2px), 50% 50%, 50% calc(0% - 2px), 50% calc(100% + 2px), 100% 50%, 100% calc(0% - 2px), 100% calc(100% + 2px);
  }
}

.add__review {
  margin-top: 10px;
  border: 5px solid #313131;
  padding: 10px;
}

.add__review h3 {
  font-weight: 500;
}

.add__review form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.add__review label {
  margin-bottom: 5px;
}

.add__review input[type="text"],
.add__review textarea {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  box-sizing: border-box;
}

.add__review button {
  padding: 10px 20px;
  background-color: #313131;
  color: white;
  border: none;
  cursor: pointer;
}

.add__review button:hover {
  background-color: #666666;
